.form {

}

.form-field {
    display: block;
    margin-bottom: 0.5rem;

    input, select, textarea {
        width: 100%;
        height: 3rem;
        background-color: #ffffff;
        padding: 0 1rem;
        font-family: $default_font;
        color: rgba(21, 21, 21, 0.3);
        font-size: 1rem;

        &:focus {
            color: $color_text;
        }
    }
    textarea {
        height: 8.75rem;
        resize: none;
        padding: 0.75rem 1rem;
    }
    &.border {
        input, select, textarea {
            border-radius: $rounded;
            border: 0.063rem solid $border;
        }
    }
}
